@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:300,300i,700,700i);
* {
  box-sizing: border-box; }

html {
  font-size: 18px;
  padding: 0;
  margin: 0; }

body {
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans', -apple-system, BlinkMacSystemFont, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5;
  background-color: #fff;
  color: #424242; }

header {
  position: fixed;
  width: 300px;
  left: 0;
  top: 0;
  padding: 30px;
  min-height: 100vh; }

main {
  margin-left: 300px;
  padding: 85px 30px 30px;
  min-height: calc(100vh - 3em); }
  main h2 {
    font-size: 2rem; }
  main > div {
    max-width: 800px; }
  main a {
    text-decoration: none;
    font-weight: bold; }

ul {
  padding: 0;
  margin: 0; }
  ul li {
    list-style: none; }

a {
  color: #424242; }

strong {
  font-weight: bold; }

h1, h2, h3, h4, h5, h6 {
  color: #212121; }

h1 {
  margin: 0; }

#title {
  font-size: 1.125rem;
  position: relative;
  padding-left: 3rem;
  margin-bottom: 1.75rem; }
  #title a {
    color: #212121;
    text-decoration: none; }

#glyph {
  font-size: 2.5rem;
  font-weight: 300;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 1.125rem; }

.nav li {
  margin-bottom: 0.125rem; }
  .nav li a {
    font-weight: bold;
    text-decoration: none;
    border-bottom: solid 1px #fff; }

.nav-social {
  margin-top: 25px; }
  .nav-social a {
    font-size: 1.67rem;
    margin-right: 0.67rem; }

.posts {
  margin-top: 40px; }

.post {
  position: relative;
  margin: 0;
  padding: 25px 25px 25px 225px;
  max-width: 800px;
  border-bottom: solid 1px #ddd;
  min-height: 8em;
  display: block;
  font-weight: inherit; }
  .post h3 a {
    text-decoration: none;
    border-bottom: none; }
  .post .post-body p {
    margin-top: 0; }
    .post .post-body p:last-of-type {
      margin-bottom: 0; }

.post-meta {
  color: #212121;
  width: 200px;
  position: absolute;
  left: 0;
  top: 21px;
  font-size: 13px;
  padding-top: 2px;
  text-align: right;
  height: 100%; }
  .post-meta h3 {
    margin: 0;
    font-size: 20px; }
  .post-meta p {
    margin: 0; }
  .post-meta .post-timestamp {
    position: absolute;
    bottom: 50px;
    right: 0; }

.expos {
  font-style: italic; }

.columnSet {
  display: flex; }

.column {
  width: 33%; }
  .column p {
    font-size: 1.2em; }
  .column ul {
    padding: 0 0.8em; }

footer {
  width: 100%;
  height: 2em;
  line-height: 2em; }

.copyright {
  text-align: center;
  font-size: 0.8em;
  padding: 0.5em 0; }

.project-tags {
  padding-top: 1em; }
  .project-tags h3 {
    display: none; }

.project-notes .posts {
  margin: 0; }

.tag-list {
  font-size: 0; }
  .tag-list a {
    display: inline-block;
    background-color: #669;
    color: #fff;
    border-bottom: none;
    padding: 1px 10px;
    border-radius: 5px;
    font-weight: bold;
    font-size: 14px;
    margin-right: 7px; }
    .post-meta .tag-list a {
      font-size: 12px;
      margin-right: 4px;
      padding: 2px 7px;
      margin-top: 4px; }
    .tag-list a:last-of-type {
      margin-right: 0; }

.single h2 {
  position: relative; }
  .single h2:after {
    position: absolute;
    font-size: 0.85rem;
    top: -1em;
    left: 0;
    opacity: 0.5; }

.tag-single h2:after {
  content: 'Tag'; }

.project-single h2:after {
  content: 'Project'; }

@media (max-width: 1024px) {
  .columnSet {
    display: block; }
  .column {
    width: 100%; }
  header {
    width: 260px; }
  main {
    margin-left: 260px; } }

@media (max-width: 799px) {
  header {
    position: static;
    width: auto;
    height: auto;
    min-height: auto;
    padding: 15px;
    background-color: #f0f0f0; }
  #title {
    margin-bottom: 0;
    width: 168px;
    margin: 0 auto; }
  .nav {
    display: none; }
  main {
    margin: 0;
    padding: 25px; } }

@media (max-width: 639px) {
  .post {
    position: static;
    padding: 25px 0; }
  .post-meta {
    position: static;
    width: 100%;
    text-align: left;
    margin-bottom: 15px; }
    .post-meta .post-timestamp {
      position: static; }
  .post-body p {
    font-size: 14px; } }

